
const Contact = () => {
    return (
        <div>
            
             <div className="my-5">
                <h1 class="text-center top-text">Bizimlə Əlaqə</h1>

                <div className="container contact-div">
                    <div className="row">
                        <div className="col-md-6 col-10 mx-auto">
                            <form>
                                <div className="mb-3">
                                    <label for="exampleFormControlInput1" className="form-label">Adınız</label>
                                    <input type="text" className="form-control" id="exampleFormControlInput1" placeholder="Adınızı qeyd edin..." />
                                </div>
                                <div className="mb-3">
                                    <label for="exampleFormControlInput1" className="form-label">Nömrəniz</label>
                                    <input type="number" className="form-control" id="exampleFormControlInput2" placeholder="Nömrənizi qeyd edin..." />
                                </div>
                                <div className="mb-3">
                                    <label for="exampleFormControlInput1" className="form-label">Email adresiniz</label>
                                    <input type="email" className="form-control" id="exampleFormControlInput3" placeholder="e-mail adresiniz..." />
                                </div>
                                <div className="mb-3">
                                    <label for="exampleFormControlTextarea1" className="form-label">Mesajınız</label>
                                    <textarea placeholder="Təklif, şikayət, düşüncələrinizi burada qeyd edin..." className="form-control" id="exampleFormControlTextarea1" rows="3"></textarea>
                                </div>
                                <div>
                                    <button className="btn btn-outline-dark" type="submit">Göndər</button>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Contact
