
const HomeCarouselProps = (props) => {
    return (
        <div className={props.class}>
            <img src={props.image} class="d-block w-100" alt="image" />
            <div class="carousel-caption d-none d-md-block">
                <h5>{props.title}</h5>
                <p>{props.description}</p>
            </div>
        </div>
    )
}

export default HomeCarouselProps
