import telegram from '../src/images/telegram.png'
import facebook from '../src/images/facebook.png'
import instagram from '../src/images/instagram.png'



const footer = () => {
  return (
    <footer class="bg-light mt-5">
      <div class="container py-5">
        <div class="row py-4">
          <div class="col-lg-4 col-md-6 mb-4 mb-lg-0" style={{textAlign:"center"}}>

            <a className="btn btn-outline-light btn-floating m-1 " href="#" role="button"
            ><img src={facebook} alt="facebook" /></a>

            <a className="btn btn-outline-light btn-floating m-1" href="#" role="button"
            ><img src={telegram} alt="telegram" /></a>

            <a className="btn btn-outline-light btn-floating m-1" href="#" role="button"
            ><img src={instagram} alt="instagram" /></a>
            <p class="font-italic text-muted mt-3">Xidmətimizi indi daha da genişləndirdik. Bizi sosial şəbəkələrdə də izləyin.</p>
            <section className="mb-4">
            </section>
          </div>
          <div class="col-lg-2 col-md-6 mb-4 mb-lg-0">
            <h6 class="text-uppercase font-weight-bold mb-4">Mağaza</h6>
            <ul class="list-unstyled mb-0">
              <li class="mb-2"><a href="#" class="text-muted">Dəhliz üçün</a></li>
              <li class="mb-2"><a href="#" class="text-muted">Mətbəx üçün</a></li>
              <li class="mb-2"><a href="#" class="text-muted">Yataq otağı üçün</a></li>
              <li class="mb-2"><a href="#" class="text-muted">Qonaq otağı üçün</a></li>
            </ul>
          </div>
          <div class="col-lg-2 col-md-6 mb-4 mb-lg-0">
            <h6 class="text-uppercase font-weight-bold mb-4">Şirkət</h6>
            <ul class="list-unstyled mb-0">
              <li class="mb-2"><a href="#" class="text-muted">Giriş</a></li>
              <li class="mb-2"><a href="#" class="text-muted">Qeydiyyat</a></li>
              <li class="mb-2"><a href="#" class="text-muted">Səbət</a></li>
              <li class="mb-2"><a href="#" class="text-muted">Məhsullarımız</a></li>
            </ul>
          </div>
          <div class="col-lg-4 col-md-6 mb-lg-0">
            <h6 class="text-uppercase font-weight-bold mb-4">Xəbərlər</h6>
            <p class="text-muted mb-4">Abunə olaraq yeniliklərdən ən birinci siz xəbərdar olun.</p>
            <div class="p-1 rounded border">
              <div class="input-group">
                <input type="email" placeholder="Email adresinizi qeyd edin" aria-describedby="button-addon1" class="form-control border-0 shadow-0" />
                <div class="input-group-append">
                  <button id="button-addon1" type="submit" class="btn btn-link"><i class="fa fa-paper-plane"></i></button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </footer>
  )
}

export default footer;
