
const Search = () => {
    return (
        <div className="container">
            <div className="row">
            <div className="col-lg-6">
                <div class="input-group mt-5">
                    <input type="search" class="form-control rounded" placeholder="axtar..." aria-label="Search"
                        aria-describedby="search-addon" />
                    <button type="button" class="btn btn-outline-dark">Axtar</button>
                </div>
            </div>
            </div>
        </div>
    )
}

export default Search;
