import logo2 from './images/logo2.PNG'
const Login = () => {
    
    return (
        <div class="wrapper bg-white">
            <img src={logo2} alt=""/>
            <div class="h4 text-muted text-center pt-2">Giriş məlumatlarınızı daxil edin</div>
            <form class="pt-3">
                <div class="form-group py-2">
                    <div class="input-field"> <span class="far fa-user p-2"></span> <input type="text" placeholder="Adınız və ya e-mail adresiniz" required class=""/> </div>
                </div>
                <div class="form-group py-1 pb-2">
                    <div class="input-field"> <span class="fas fa-lock p-2"></span> <input type="text" placeholder="Şifrənizi daxil edin" required class=""/> <button class="btn bg-white text-muted"> <span class="far fa-eye-slash"></span> </button> </div>
                </div>
                <div class="d-flex align-items-start">
                    <div class="remember"> <label class="option text-muted"> Xatırla <input type="radio" name="radio"/> <span class="checkmark"></span> </label> </div>
                    <div class="ml-auto"> <a href="#" id="forgot">Parolunuzu unutdunuz?</a> </div>
                </div> <button class="btn btn-outline-dark text-center my-3">Daxil ol</button>
                <div class="text-center pt-3 text-muted">Hesabınız yoxdur? <a href="#">Qeydiyyat</a></div>
            </form>
        </div>
    )
}

export default Login;
