import { NavLink } from 'react-router-dom'
import logo from './images/logo.PNG'
import loupe from './images/loupe.png'
import user from './images/user.png'
import cart from './images/cart.png'

const Navbar = () => {
    function click() {
        document.querySelector(".menu").classList.remove("show")
    }
    return (
        <nav class="navbar fixed-top navbar-expand-lg navbar-light bg-white">
            <div class="container">
                <NavLink to="/theialand" className="first-logo" style={{ display: "contents" }}><img class="navbar-brand" src={logo} alt="" /></NavLink>
                
                <div class="d-flex hidden-icons">
                    <NavLink to="/login"><img src={user} alt="user" /></NavLink>
                    <NavLink to="/search"><img src={loupe} alt="loupe" /></NavLink>
                    <NavLink to="/cart"><img src={cart} alt="cart" /></NavLink>
                </div>
                <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span class="navbar-toggler-icon"></span>
                </button>
                <div class="collapse navbar-collapse menu" id="navbarSupportedContent">

                    <ul class="navbar-nav me-auto mb-2 mb-lg-0">
                        <li class="nav-item" onClick={click}>
                            <NavLink class="nav-link active" aria-current="page" to="/">Ana Səhifə</NavLink>
                        </li>
                        <li class="nav-item" onClick={click}>
                            <NavLink to="/about"><a class="nav-link" to="/#" >Haqqımızda </a></NavLink>
                        </li>
                        <li class="nav-item" onClick={click}>
                            <NavLink to="/service"><a class="nav-link" href="#">Servis</a></NavLink>
                        </li>
                        <li class="nav-item dropdown" onClick={click}>
                            <a class="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                                Mağaza
                             </a>
                            <ul class="dropdown-menu" aria-labelledby="navbarDropdown">
                                <li><NavLink class="dropdown-item" to="/market"><a href="">Hamısı</a></NavLink></li>
                                <li><a class="dropdown-item" href="#">Stol</a></li>
                                <li><a class="dropdown-item" href="#">Stul</a></li>
                                <li><a class="dropdown-item" href="#">Pərdə</a></li>
                                <li><a class="dropdown-item" href="#">Çilçıraq</a></li>
                                <li><a class="dropdown-item" href="#">Yataq Dəstləri</a></li>
                                <li><a class="dropdown-item" href="#">Duş Kabinləri</a></li>
                            </ul>
                        </li>
                        <li class="nav-item" onClick={click}>
                            <NavLink to="/contact"><a class="nav-link" href="#">Əlaqə</a></NavLink>
                        </li>
                    </ul>
                    <div class="d-flex top-icons">
                        <NavLink to="/login"><img src={user} alt="user" /></NavLink>
                        <NavLink to="/search"><img src={loupe} alt="loupe" /></NavLink>
                        <NavLink to="/cart"><img src={cart} alt="cart" /></NavLink>
                    </div>
                </div>
            </div>
        </nav>
    )
}

export default Navbar;
