import { NavLink } from 'react-router-dom'



const HomeCardProps2 = (props) => {
    return (
        <div class="col-md-4 col-sm-6">
            <div class="pricingTable">
                <div class="pricingTable-header">
                    <h3 class="heading">{props.type}</h3>
                    <span class="subtitle">{props.description}</span>
                    <div class="price-value">{props.month}
                        <span class="currency">₼</span>
                        <span class="month">/ay</span>
                    </div>
                </div>
                <ul class="pricing-content">
                    <li>{props.des1}</li>
                    <li>{props.des2}</li>
                    <li>{props.des3}</li>
                    <li>{props.des4}</li>
                </ul>
                <NavLink to="/login"><a href="#" class="read">Üzv ol<i class="fa fa-angle-right"></i></a></NavLink>
            </div>
        </div>
    )
}

export default HomeCardProps2;
