import { NavLink } from 'react-router-dom'
import cart2 from './images/cart2.png'


const HomeCardProps = (props) => {
    return (
            <div class="col-md-3 col-sm-6">
                        <div class="product-grid4">
                            <div class="product-image4">
                                <a href="#">
                                    <img class="pic-1" src={props.image1} />
                                    <img class="pic-2" src={props.image2} />
                                </a>
                                <ul class="social">
                                    <li><a href="#" data-tip="Quick View"><i class="fa fa-eye"></i></a></li>
                                    <li><a href="#" data-tip="Add to Wishlist"><i class="fa fa-shopping-bag"></i></a></li>
                                    <li><a href="#" data-tip="Add to Cart"><i class="fa fa-shopping-cart"></i></a></li>
                                </ul>
                                <span class="product-new-label">{props.info}</span>
                                <span class="product-discount-label">{props.discount}</span>
                            </div>
                            <div class="product-content">
                                <h3 class="title"><a href="#">{props.title}</a></h3>
                                <div class="price">
                                {props.price} <span>{props.previousPrice}</span>
                                </div>
                                <div class="buttons d-flex flex-row">
                                    <div class="cart">
                                      <NavLink to='/cart'><img src={cart2} alt=""/></NavLink>  
                                    </div>
                                    <button class="btn btn-dark cart-button"><span class="dot">1</span>Səbətə əlavə et</button>
                                </div>
                            </div>
                        </div>
                    </div>  
    )
}

export default HomeCardProps;
